import React,{useState} from 'react';
import './App.css';
import './coustom.css';
import About from './components/About';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
import Alert from './components/Alert';
import { BrowserRouter, Routes, Route } from "react-router-dom";
  


  function App() {
  const [mode, setMode] = useState("Light");
  const [navmode, setnavMode] = useState("navbar navbar-expand-lg my-nav");
  const [navtext, setnavText] = useState("nav-link1");
  const [btnmode, setbtnMode] = useState("my-btn ");
  const [alert, setalert] = useState(null);
  const showalert = (type, message, alertclass) => {
    setalert({
      mes: message,
      type: type,
      alertcls: alertclass,
    });
    setTimeout(() => {
      setalert(null);
    }, 1500);
  };

  const togglemode = () => {
    if (mode === "Light") {
      setMode("Dark");
      document.body.style.backgroundColor = "#010c14";
      document.body.style.color = "white";
      document.title = "TextUtils - Dark Mode";
      setnavMode("navbar navbar-expand-lg  bg-dark border-bottom border-body");
      setnavText("nav-link2");
      setbtnMode("btn-outline-secondary my-btns");
      showalert("successfully", "Dark mode has been enabled", "success");
    } else {
      setMode("Light");
      document.body.style.backgroundColor = "white";
      document.body.style.color = "black";
      document.title = "TextUtils - Light Mode";
      setnavMode("navbar navbar-expand-lg my-nav");
      setnavText("nav-link1");
      setbtnMode("my-btn");
      showalert("successfully", "Light mode has been enabled", "primary");
    }
  };
  return (
    <BrowserRouter>
    <Navbar aboutText='About' title="TextUtils" mode={mode} navmode={navmode} navtext={navtext} btnmode={btnmode} togglemode={togglemode} />
    <Alert alert={alert} />
    <div className="container my-3">
    <Routes>
      <Route exact path="/" element={<TextForm heading="Enter Your Text To Analyze" showalert={showalert} mode={mode} btnmode={btnmode} />} />
      <Route exact path="/about" element={<About mode={mode} />} />
    </Routes>
    </div>
    </BrowserRouter>
   
  );
}

export default App;
