import { toHaveStyle } from '@testing-library/jest-dom/matchers';
import React,{useState} from 'react'

export default function About(props) {
        

    const [mystyle, setmystyle] = useState({
        color: "black",
        backgroundColor: "white",
      
      });
      const [btntext ,setmybtntext] = useState("Enable Dark Mode")
      const [btnclass, setbtnclass] = useState("btn btn-primary my-btn my-3")
      
      const togglestyle = () => {
        if (mystyle.color === "black") {
          setmystyle({
            color: "white",
            backgroundColor: "black",
          });
          setmybtntext("Enable Light Mode")
          setbtnclass("btn btn-light my-3")
        } else {
          setmystyle({
            color: "black",
            backgroundColor: "white",
          });
          setmybtntext("Enable Dark Mode")
          setbtnclass("btn btn-primary my-btn my-3")
        }
      };
  return (
    <div className="container " style={mystyle}>
    <h1 className="mb-3">About Us</h1>
    <div className="accordion" id="accordionExample" style={mystyle}>
      <div className="accordion-item">
        <h2 className="accordion-header">
          <button
            className="accordion-button"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseOne"
            aria-expanded="true"
            aria-controls="collapseOne"
            style={mystyle}
          >
           My Accordion
          </button>
        </h2>
        <div
          id="collapseOne"
          className="accordion-collapse collapse show"
          data-bs-parent="#accordionExample"
        >
          <div className="accordion-body" style={mystyle}>
            <strong>This app is built with React.</strong> It is
            shown by default, until the collapse plugin adds the appropriate
            classes that we use to style each element. These classes control
            the overall appearance, as well as the showing and hiding via CSS
            transitions. You can modify any of this with custom CSS or
            overriding our default variables. It's also worth noting that just
            about any HTML can go within the <code>.TextUtils-body</code>,
            though the transition does limit overflow.
          </div>
        </div>
      </div>
      <div className="accordion-item">
        <h2 className="accordion-header">
          <button
            className="accordion-button collapsed"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseTwo"
            aria-expanded="false"
            aria-controls="collapseTwo"
            style={mystyle}
          >
            Something About My TextUtils App
          </button>
        </h2>
        <div
          id="collapseTwo"
          className="accordion-collapse collapse"
          data-bs-parent="#accordionExample"
        >
          <div className="accordion-body" style={mystyle}>
            <strong>React Text Utils is a web application built with React that offers a set of text manipulation tools. The application provides various text utilities such as text case conversion, string reverse, remove extra spaces, and more. The website is designed with a clean and user-friendly interface to provide a seamless experience for users.</strong>  <code>.About Us-body</code>,
            This App great user Exprience
          </div>
        </div>
      </div>
      <div className="accordion-item">
        <h2 className="accordion-header">
          <button
            className="accordion-button collapsed"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseThree"
            aria-expanded="false"
            aria-controls="collapseThree"
            style={mystyle}
          >
            MY Exprience
          </button>
        </h2>
        <div
          id="collapseThree"
          className="accordion-collapse collapse"
          data-bs-parent="#accordionExample"
        >
          <div className="accordion-body" style={mystyle}>
            <strong>My experience About My TextUtils App</strong> This is My first app built with React. My experience Creating this application leran somthing most interting  things about React Framework and it's use.     <code>.accordion-body</code>,
            This App provide the great user Exprience
          </div>
        </div>
      </div>
    </div> 
    <div className=" container ps-0">
      <button onClick={togglestyle} type="button" className={btnclass} >
       {btntext}
      </button>
    </div>     
  </div>
  )
}

